import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Default from '@/components/layouts/Default'
import Baseline from '@/components/layouts/Baseline'
import BaselineFlipped from '@/components/layouts/BaselineFlipped'
import Centered from '@/components/layouts/Centered'
import Complex from '@/components/layouts/Complex'
import Dark from '@/components/layouts/Dark'
import GoogleContacts from '@/components/layouts/GoogleContacts'
import GoogleKeep from '@/components/layouts/GoogleKeep'
import GoogleYoutube from '@/components/layouts/GoogleYoutube'
import Sandbox from '@/components/layouts/Sandbox'
import Test from '@/components/Test'

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', name: 'HelloWorld', component: HelloWorld },
    { path: '/default', name: 'Default', component: Default },
    { path: '/baseline', name: 'Baseline', component: Baseline },
    { path: '/baselineflipped', name: 'BaselineFlipped', component: BaselineFlipped },
    { path: '/centered', name: 'Centered', component: Centered },
    { path: '/complex', name: 'Complex', component: Complex },
    { path: '/dark', name: 'Dark', component: Dark },
    { path: '/googlecontacts', name: 'GoogleContacts', component: GoogleContacts },
    { path: '/googlekeep', name: 'GoogleKeep', component: GoogleKeep },
    { path: '/googleyoutube', name: 'GoogleYoutube', component: GoogleYoutube },
    { path: '/sandbox', name: 'Sandbox', component: Sandbox },

    { path: '/test', name: 'Test', component: Test }
  ]
})



